import { Component, OnInit, OnChanges, SimpleChanges} from '@angular/core';
import  {Joke} from '../../plunk/joke/joke';

@Component({
  selector: 'app-joke-list',
  templateUrl: './joke-list.component.html',
  styleUrls: ['./joke-list.component.css']
})
export class JokeListComponent implements OnInit, OnChanges {

  public jokes: Joke[] = [];

  constructor() { 
    this.jokes = [
      new Joke("What did the cheese say when it looked in the mirror?", "Hello-Me (Halloumi)"),
      new Joke("What kind of cheese do you use to disguise a small horse?", "Mask-a-pony (Mascarpone)"),
      new Joke("A kid threw a lump of cheddar at me", "I thought ‘That’s not very mature’")
    ]
  }

  ngOnInit() {
  }

  ngOnChanges() {
  }

  addJoke(joke) {

    // console.log(new Joke("What did the cheese say when it looked in the mirror", "Hello-me (Halloumi)"));
    this.jokes.unshift(new Joke("What did the cheese say when it looked in the mirror", "Hello-me (Halloumi)"));
  }

  deleteJoke() {
    this.jokes = [];
  }

}
